}

.modal {
  width: 100%;
  max-width: 450px;
  height: auto;
  background-color: var(--background-mobile-menu);
  background-image: linear-gradient(to right, rgba(225, 121, 146, .7), rgba(225, 121, 146, .9)), url("slider-pic1.6154618a.png");
