@keyframes changeBgColor {
  0% {
    background-color: var(--background-primary-color);
  }

  50% {
    background-color: var(--white-font-color);
  }

  100% {
    background-color: var(--accent-font-color);
  }
}

.checkbox {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.checkbox__lable {
  letter-spacing: .03em;
  color: var(--white-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1.75;
  display: block;
}

.checkbox + label {
  cursor: pointer;
  padding: 0 0 0 65px;
  position: relative;
}

.checkbox + label:before {
  content: "";
  width: 38px;
  height: 20px;
  background: var(--background-button-hover);
  border-radius: 13px;
  transition: all .2s;
  position: absolute;
  top: 5px;
  left: 210px;
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .checkbox + label:before {
    left: 241px;
  }
}

.checkbox + label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--reverse-background-button);
  border-radius: 10px;
  transition: all .2s;
  position: absolute;
  top: 6px;
  left: 212px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .3);
}

@media screen and (min-width: 768px) {
  .checkbox + label:after {
    left: 242px;
  }
}

.checkbox:checked + label:before {
  background: var(--icon-background);
}

.checkbox:checked + label:after {
  left: 230px;
}

@media screen and (min-width: 768px) {
  .checkbox:checked + label:after {
    left: 259px;
  }
}

.checkbox:focus + label:before {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .2), 0 0 0 2px rgba(194, 226, 151, .7);
}

