.burger-menu {
  width: 100%;
  height: 2px;
  background-color: var(--text-button);
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 0;
}

.burger-menu:before, .burger-menu:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--text-button);
  z-index: 120;
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  left: 0;
}

